	import {
		mapState,
		mapMutations,
		mapActions
	} from 'vuex'
	import {
		pathToBase64,
		base64ToPath
	} from '../../../js_sdk/gsq-image-tools/image-tools/index.js'
	export default {
		data() {
			return {
				userName: "",
				userList: [{
						front: "../../../static/resive.png",
						title: "修改/完善个人信息",
						src: "../../../static/right.png"
					},
					{
						front: "../../../static/comment.png",
						title: "我的评论",
						src: "../../../static/right.png"
					},
					{
						front: "../../../static/collect.png",
						title: "我的收藏",
						src: "../../../static/right.png"
					}
				],
				id: "",
				name: "",
				email: "",
				userImg: "",
				initImg: ""
			}
		},
		computed: {
			...mapState(["changeImg"])
		},
		methods: {
			...mapMutations(["changeAvator"]),
			...mapActions(['getUserMsg']),
			layout(){
				uni.clearStorageSync();
			},
			toUser(index) {
				switch (index) {
					case 0:
						if (this.id) {
							uni.navigateTo({
								url: "./profile"
							})
						} else {
							uni.navigateTo({
								url: "./login"
							})

						}

						break;
					case 1:

						if (this.id) {
							uni.navigateTo({
								url: "./comment"
							})
						} else {
							uni.navigateTo({
								url: "./login"
							})

						}
						break;
					case 2:

						if (this.id) {
							uni.navigateTo({
								url: "./collection"
							})
						} else {
							uni.navigateTo({
								url: "./login"
							})

						}
						break;
				}

			},
			login() {
				if (this.id) {
					return
				} else {
					uni.navigateTo({
						url: './login'
					})
				}
			},
			selectImg() {
				if (!this.id) {
					return
				} else {
					let that = this
					uni.chooseImage({
						count: 1,
						sizeType: ['original', 'compressed'],
						sourceType: ['album'],
						success: function(res) {
							pathToBase64(res.tempFilePaths[0]).then((base64) => {
								uni.request({
									url: "http://47.96.156.131:5000/keep/users/avator",
									method: "POST",
									data: {
										_id: that.id,
										image: base64
									},
									success: (res) => {
										that.userImg = res.data.avatar
									},

									fail: () => {
										uni.showToast({
											icon: 'none',
											title: '上传图片过大'
										})
									}
								})
							})
						},
						fail: () => {
							uni.showToast({
								icon: 'none',
								title: '上传失败'
							})
						}
					})
				}

			},
			dynamics() {
				if(this.id){
					uni.navigateTo({
					url: './dynamics'
				})
				}
				else {
					uni.navigateTo({
						url: './login'
					})
				}
				
			}
		},
		onShow() {
			let currentTime = Date.now() / 1000
			try {
				uni.getStorage({
					key: "tiatok",
					success: (res => {
						// 拿着 token  去 请求 current  拿到 id 
						// 先发起一个请求 
						uni.request({
							url: "http://47.96.156.131:5000/keep/users/willjwt",
							method: "POST",
							data: {
								current: currentTime,
								jwtd: JSON.parse(res.data).Token
							},
							success: (result) => {
								if (result.data.callbackMsg) {
									uni.request({
										url: "http://47.96.156.131:5000/keep/users/current",
										method: "GET",
										header: {
											'Authorization': JSON.parse(res.data).Token
										},
										success: (res) => {
											
											this.id = res.data.id
											this.name = res.data.name
											this.email = res.data.email
											this.userImg = res.data.avatar
											this.initImg = "../../../static/我的.png"
										}
									})

								} else {
									this.name = "点击登录"
									try {
										uni.clearStorageSync();
									} catch (e) {
                                       return 
									}
								}
							},
							fail: () => {

							}
						})

					})
				})
			} catch (e) {
				console.log(e)
			}

		}
	}
